html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  color: #FFFFFF;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background: #000000;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

section {
  margin-bottom: 5rem;
}

section:last-of-type {
  margin-bottom: 0em;
}

.contenedor-margen {
  margin: auto;
  max-width: 1200px;
}

.main-contacto {
  margin-bottom: 7rem;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  gap: 3rem;
}
.form .btn-blanco {
  width: 100%;
}

.contenedor-label {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin: 1rem 2rem 1rem 1rem;
}
.contenedor-label input,
.contenedor-label textarea,
.contenedor-label select {
  width: 100%;
  padding: 0.5rem;
  border-radius: 4px;
  background-color: dimgray;
}
.contenedor-label input::placeholder,
.contenedor-label textarea::placeholder,
.contenedor-label select::placeholder {
  color: #FFFFFF;
}
.contenedor-label textarea {
  max-height: 4rem;
}

.label {
  width: 15rem;
  text-align: end;
}

.header {
  background-color: #000000;
  padding: 0 3rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  gap: 1rem;
}

.header__title {
  color: #FFFFFF;
  font-weight: 800;
}

.list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 3rem;
}

.list__item a {
  color: #FFFFFF;
  transition: opacity 0.3s;
}
.list__item a:hover {
  opacity: 0.7;
}

.cover {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.cover__content {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 2rem;
  padding-top: 7rem;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0));
}

.cover__content__title {
  font-weight: 900;
  font-size: 4.8rem;
  color: #FFFFFF;
  text-transform: uppercase;
  margin: 0;
}

.cover__content__paragraph {
  font-weight: 700;
  font-size: 2.4rem;
  width: 100rem;
  color: #FFFFFF;
  text-align: center;
}

.cover__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

.news {
  position: relative;
}

.container-triangle {
  position: absolute;
  max-width: 1200px;
  height: 80rem;
  background-color: orange;
}

.triangle1 {
  position: absolute;
  width: 30rem;
  height: 30rem;
  top: 20rem;
  left: 30rem;
  background: rgb(2, 0, 36);
  background: -moz-linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  background: -webkit-linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  background: linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#020024",endColorstr="#00d4ff",GradientType=1);
  animation: t1 16s ease infinite alternate-reverse;
}

.triangle2 {
  position: absolute;
  width: 20rem;
  height: 20rem;
  top: 45rem;
  left: 108rem;
  background: rgb(2, 0, 36);
  background: -moz-linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  background: -webkit-linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  background: linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#020024",endColorstr="#00d4ff",GradientType=1);
  animation: t2 20s ease infinite alternate-reverse;
}

.triangle3 {
  position: absolute;
  width: 25rem;
  height: 25rem;
  top: 37rem;
  left: 70rem;
  background: rgb(2, 0, 36);
  background: -moz-linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  background: -webkit-linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  background: linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#020024",endColorstr="#00d4ff",GradientType=1);
  animation: t3 24s ease infinite alternate-reverse;
}

.triangle4 {
  position: absolute;
  width: 35rem;
  height: 35rem;
  top: -8rem;
  left: 80rem;
  background: rgb(2, 0, 36);
  background: -moz-linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  background: -webkit-linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  background: linear-gradient(151deg, rgb(2, 0, 36) 0%, rgb(185, 12, 12) 24%, rgb(0, 212, 255) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#020024",endColorstr="#00d4ff",GradientType=1);
  animation: t4 30s ease infinite alternate-reverse;
}

@keyframes t1 {
  0% {
    transform: rotate(58deg) translateY(0px);
  }
  50% {
    transform: rotate(55deg) translateY(25px);
  }
  100% {
    transform: rotate(58deg) translateY(0px);
  }
}
@keyframes t2 {
  0% {
    transform: rotate(53deg) translateY(0px);
  }
  50% {
    transform: rotate(55deg) translateY(30px);
  }
  100% {
    transform: rotate(53deg) translateY(0px);
  }
}
@keyframes t3 {
  0% {
    transform: rotate(45deg) translateY(0px);
  }
  50% {
    transform: rotate(50deg) translateY(20px);
  }
  100% {
    transform: rotate(45deg) translateY(0px);
  }
}
@keyframes t4 {
  0% {
    transform: rotate(50deg) translateY(0px);
  }
  50% {
    transform: rotate(55deg) translateY(40px);
  }
  100% {
    transform: rotate(50deg) translateY(0px);
  }
}
.new-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  gap: 3rem;
  z-index: 100;
}

.news__title {
  font-weight: 700;
  color: #ff1b1b;
  margin: 0;
  z-index: 1000;
}

.card {
  max-width: 40rem;
  border-radius: 4px;
  overflow: hidden;
  padding: 2rem;
  box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  -webkit-box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  -moz-box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  transition: 0.4s;
  z-index: 100;
  border-top: 2px solid rgba(255, 255, 255, 0.3);
  border-left: 2px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
}
.card .card__content__title {
  color: #FFFFFF;
}
.card .card__content__paragraph {
  color: #FFFFFF;
}
.card:hover {
  background: #ff1b1b;
  transform: translateY(-10px);
}
.card:hover .card-img {
  width: 110%;
}

.card__content-img {
  width: 100%;
  border-radius: 4px;
  overflow: hidden;
}

.card-img {
  width: 100%;
  object-fit: cover;
  object-position: center;
  transition: 0.4s;
}

.card__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.card__content__title {
  font-weight: 400;
  margin: 0;
}

.card__content__paragraph {
  text-align: justify;
}

.modelos {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  gap: 0;
  background-color: #000000;
}

.modelos__title {
  color: #FFFFFF;
  font-weight: 900;
}

.modelo {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0;
}

.modelo__img {
  width: 50%;
}

.modelo__img img {
  width: 100%;
  height: 70rem;
  object-fit: cover;
  object-position: center;
}

.modelo-description {
  width: 50%;
  padding: 4rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 1rem;
}

.modelo-description__title {
  color: #FFFFFF;
  font-weight: 700;
  text-align: left;
}

.modelo-description__paragraph {
  color: #FFFFFF;
  font-weight: 400;
  max-width: 50rem;
  text-align: justify;
}

.modelo-description__price {
  color: #FFFFFF;
  font-weight: 700;
  max-width: 50rem;
}

.contacto {
  position: relative;
  width: 100%;
  height: 30rem;
}

.contacto__content {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.contacto__content__title {
  color: #FFFFFF;
  font-weight: 900;
  margin: 0;
}

.contacto__content__paragraph {
  color: #FFFFFF;
}

.content__video {
  position: absolute;
  width: 100%;
  max-height: 100%;
  object-fit: cover;
  object-position: center center;
  z-index: -1;
}

.footer {
  background-color: #000000;
  z-index: 100;
}
.footer ul {
  justify-content: space-evenly;
}
.footer ul a {
  transition: opacity 0.3s;
}
.footer ul a:hover {
  opacity: 0.7;
}
.footer a {
  color: #FFFFFF;
}

.copyrigth {
  color: #FFFFFF;
  text-align: center;
}